<template>
  <div style="margin-top: 10px" v-loading="fullloading" id="base">

    <div style="margin-top: 20px">
      <el-table :data="menuList" size="mini" border stripe @selection-change="handleSelectionChange" v-loading="tableLoading" style="width: 100%">
        <el-table-column type="selection" align="left">
        </el-table-column>
        <el-table-column prop="menuName" align="center" label="菜单名称">
        </el-table-column>
        <el-table-column prop="url" align="center" label="链接">
        </el-table-column>
        <el-table-column prop="component" align="center" label="组件">
        </el-table-column>
        <el-table-column prop="iconCls" align="center"label="图标">
          <template slot-scope="scope">
            <i slot="prefix" :class="[scope.row.iconCls]"></i>
          </template>
        </el-table-column>

        <el-table-column label="操作" align="center" width="300" fixed="right">
          <template slot-scope="scope">
            <el-button @click="getList(scope.row.id,true)" type="primary" style="padding: 6px 10px;margin: 2px"
                       size="mini" v-show="!mainPage">查看子菜单
            </el-button>
            <el-button @click="update(scope.row,{title:'修改'})" type="primary" style="padding: 6px 10px;margin: 2px"
                       size="mini">修改
            </el-button>
            <el-button @click="deleteMsg(scope.row.id)" type="danger" style="padding: 6px 10px;margin: 2px"
                       size="mini">删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="clearfix">
      <div class="fl" style="margin-top: 20px">
        <el-button size="mini" @click="add" type="success" icon="el-icon-search">新增</el-button>
        <el-button size="mini" @click="getList(1,false)" icon="el-icon-back" v-show="mainPage">返回</el-button>
      </div>
    </div>

    <!--新增或修改弹框-->
    <el-form style="margin: 0px;padding: 0px;">
      <div style="text-align: left" id="Bdialog">
        <el-dialog
          style="padding: 0px;"
          :close-on-click-modal="false"
          :visible.sync="dialogVisible"
          width="50%">
          <div class="dialog_B Detail">
            <div class="" style="width: 700px;margin: auto">
              <h1 class="H1">{{dialogTitle}}</h1>
              <el-form style="margin: 0px;padding: 0px;">
                <el-form-item label="菜单名称：" prop="menuName" :label-width="labelWidth">
                  <el-input placeholder="请输入菜单名称" prefix-icon="el-icon-edit" v-model="menu.menuName"></el-input>
                </el-form-item>
                <el-form-item label="链接：" prop="url" :label-width="labelWidth">
                  <el-input placeholder="请输入链接" prefix-icon="el-icon-edit" v-model="menu.url"></el-input>
                </el-form-item>
                <el-form-item label="组件：" prop="component" :label-width="labelWidth">
                  <el-input placeholder="请输入组件" prefix-icon="el-icon-edit" v-model="menu.component"></el-input>
                </el-form-item>
                <el-form-item label="序号：" prop="menuOrder" :label-width="labelWidth">
                  <el-input placeholder="请输入序号" prefix-icon="el-icon-edit" type="number" min="1" v-model="menu.menuOrder"></el-input>
                </el-form-item>
                <el-form-item label="图标：" prop="iconCls" :label-width="labelWidth">
                  <el-button class="setLeft" type="primary" plain @click="openIcon"><i :class="[iconCls]" ></i></el-button>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer" style="text-align: right;margin-top: 20px;width: 100%;display: block">
                <el-button size="small" @click="cancelEidt()">取 消</el-button>
                <el-button size="small" type="primary" @click="sendMsg()">确 定</el-button>
              </span>
            </div>
          </div>
        </el-dialog>
      </div>
    </el-form>

    <!--图标弹框-->
    <el-dialog
      title="请选择图标"
      :visible.sync="iconVisible"
      width="30%"
      :before-close="handleClose">
      <!--<span>请选择图标</span>-->
      <ul class="iconUl">
        <li v-for="icon in icons" class="setLeft">
          <el-button type="primary" plain  @click="checkIcon(icon.name)"><i :class="[icon.name]" ></i></el-button>
        </li>
      </ul>
      <span slot="footer" class="dialog-footer">
              <el-button @click="iconVisible = false">取 消</el-button>
            </span>
    </el-dialog>

  </div>
</template>
<script>
  export default{
    data(){
      return {
        labelWidth:'100px',
        tableLoading: false,
        fullloading: false,
        dialogTitle: '',//表单标题
        dialogVisible:false,//修改的表单的弹框
        iconVisible:false,//图标弹框
        addbool: false,
        keywords: '',
        menuList:[],
        menu:{
          id: null,
          menuName:"",//菜单名称
          url:"",//链接
          component:"",//组件
          menuOrder:"",//序号
          iconCls:"",//图标
          parentId:"",//父Id
        },
        parentId:"",
        iconCls:'fa fa-plus',
        icons:[
        ],
        mainPage:true,  //是否在首页
        idstr:'',
        total: -1,  //总页数
        page: 1,  //当前页
        size:6,   //每页多少条
      }
    },
    mounted: function () {
      this.getList('1',false);
    },
    methods: {
      //获取列表
      getList(parentId,mainPage){
        let _this = this;
        this.parentId = parentId;
        this.mainPage = mainPage;
        this.tableLoading = true;
        this.$http.get("/system/menu/getByParentId/"+parentId).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            if(resp.data.status == 1){//成功
              _this.menuList = resp.data.data; //列表数据
            }else{//失败
              this.$message.error("查询失败");
            }
          }
        }).catch((error=>{
          if (error.response.status == '403') {
            this.$router.push({path: '/forbidden403'}); // 对象
          }
        }))
      },

      //添加信息的预处理
      add(){
        this.menu = {};
        this.addbool = true;
        this.dialogVisible = true;
        this.dialogTitle = "新增";
      },
      //修改信息的预处理
      update(val,obj){
        this.dialogVisible = true;//弹框
        this.addbool = false;
        this.dialogTitle = obj.title;
        this.menu = window.clone(val);//克隆
        this.iconCls = val.iconCls == undefined?"fa fa-plus":val.iconCls;
      },

      searchClick(){
        alert("ff")
      },
      //选择框
      handleSelectionChange(val) {
        var idstr = "";
        for(var　i in val ){
          idstr+=val[i].id+","
        }
        this.idstr = idstr;
      },

      //取消调用弹框的方法
      cancelEidt(){
        this.dialogVisible = false;
      },
      sendMsg(){
        if(this.addbool){
          this.postMsg();
        }else{
          this.putMsg();
        }
      },

      //新增信息   post请求
      postMsg(){
        this.menu.parentId = this.parentId; //当前父id
        this.menu.iconCls = this.menu.iconCls == undefined? "fa fa-plus":this.iconCls;
        let params = this.menu;
        let _this = this;
        this.$http({
          method:"post",
          url:"/system/menu/save",
          data:params
        }).then((response)=> {
          let data = response.data;
          if(data.status == 1){ //添加成功
            this.dialogVisible = false;
            this.$message({
              message:'新增成功',
              type:'success'
            });
            this.getList(_this.parentId,_this.mainPage);
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('新增失败');
          }
        }).catch((error)=>{
          console.log(error);
        })
      },
      //修改信息   put请求
      putMsg(){
        let params = this.menu;
        let _this = this;
        this.$http({
          method:"put",
          url:"/system/menu/update",
          data:params
        }).then((response)=> {
          let data = response.data;
          if(data.status == 1){ //添加成功
            this.dialogVisible = false;
            this.$message({
              message:'修改成功',
              type:'success'
            });
            this.getList(_this.parentId,_this.mainPage);
          }else {
            data.msg!=null?this.$message.error(data.msg):this.$message.error('修改失败');
          }
        }).catch((error)=>{
          console.log(error);
        })
      },
      //删除数据  delete请求
      deleteMsg(val){
        let _this = this;
        this.$confirm('确定删除此操作?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          _this.$http({
            method:"delete",
            url:"/system/menu/delete/"+val
          }).then((response)=>{
            let data = response.data;
            if(data.status == 1){ //删除成功
              this.$message({
                message:'删除成功',
                type:'success'
              });
              this.getList(_this.parentId,_this.mainPage);
            }else {
              data.msg!=null?this.$message.error(data.msg):this.$message.error('删除失败');
            }
          }).catch((error)=>{
            console.log(error);
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },


      handleClose(){
        this.iconVisible = false;
      },
      /*打开图标选择弹窗*/
      openIcon(){
        this.iconVisible = true;
        this.$http({
          method:'get',
          url:'/optionalIcon/getAll'
        }).then((response=>{
          let data = response.data;
          if(data.status == 1){
            this.icons = data.data;
          }

        })).catch((error)=>{
        });

      },
      /*选择图标*/
      checkIcon(icon){
        this.iconVisible = false;
        this.menu.iconCls = icon;
        this.iconCls = icon;
        console.log(icon)
      },

    }
  }
</script>
<style lang="less">
  #base{
    .el-upload-list--picture-card .el-upload-list__item{
      width: 80px;
      height: 80px;
    }
    .el-upload--picture-card{
      width: 80px;height: 80px;
      line-height: 86px;
    }
    .el-upload-list--picture-card{
      float: left;
    }
    .el-form-item__label{
      color: #333333;
      font-weight: bold;
      padding: 2px;
    }

    .el-input{
      width: 80%;
    }
    .el-dialog{
      margin-top: 5vh !important;
      min-width: 900px;
    }
  }

  #Bdialog{
    .H1{
      font: normal normal bold 28px/28px "Adobe 黑体 Std R";
      color: #333333;
      margin-bottom: 30px;
      margin-top: 5px;
    }
    .leader{
      text-align: left;
      margin: 20px 0 ;
      color: @warning;
      .l_bar{
        width: 7px;
        font-size: 18px;
        float: left;
        line-height: 18px;
        height: 21px;
        display: inline-block;
        background: @success;
        margin: 3px 3px 0 0;
      }
    }
    .dialog_B{
      max-height: 700px;
      padding-bottom: 30px;
      overflow-y: scroll;
    }
    .div_main{
      width: 700px;
      margin: auto;
      padding-bottom: 50px;
    }
    .Detail{
      .el-form-item__label{
        font-size: 16px;
        color: #333333;
      }
      .lb_v{
        font-size: 16px;
      }
    }
    .dialog_B::-webkit-scrollbar{
      display:none;
    }
    .el-form-item__content{
      text-align: left;
    }
    .el-dialog__body{
      padding: 6px 20px;
    }
  }


  .el-icon-circle-plus-outline:before {
    margin-right: 5px;
  }
  .setLeft{
    float: left;
  }

  .iconUl li{
    list-style: none;
    margin: 3px 5px;
    box-sizing: border-box;
    width: 60px;
  }
  .iconUl:after{
    content: "";
    height: 0;
    display: block;
    clear: both;
  }
  .iconUl .el-button{
    width: 100%;
  }
</style>



